.js-tabs {
   &-for {
      position: relative;
      > div {
         display: none;
         &:first-child {
            display: block; } } }

   &-nav {
      @include nosel; }
   &-nav > button,
   &-nav > a {
      &.is-active {
         pointer-events: none; }
      &.is-disabled {
         pointer-events: none;
         opacity: 0.5;
         background-color: $white; } } }

// *** begin tabs *** //
.tabs {
   $parent: &;
   &__nav {
      margin-bottom: 30px;
      @include sm {
         display: flex;
         justify-content: space-between;
         margin-bottom: 20px; }
      #{$parent}_underlined & {
         padding-bottom: 10px;
         border-bottom: 1px solid #e2e1df;
         @include sm {
            flex-direction: column;
            padding-bottom: 0; } }
      &-btn {
         position: relative;
         @include f('pt700', 21);
         color: $color7;
         transition: .3s ease-in-out;
         @include sm {
            font-size: 14px;
            flex: 1 1 auto;
            padding: 0 10px; }

         &::after {
            position: absolute;
            right: -11px;
            top: 4px;
            height: 20px;
            width: 2px;
            background-color: $color1;
            content: '';
            @include sm {
               top: 2px;
               right: -2px;
               height: 14px; }

            #{$parent}_sm & {
               top: 3px;
               height: 15px; } }

         &:hover {
            color: $color1; }

         &:last-child::after {
            display: none; }

         &:not(:last-child) {
            margin-right: 20px;
            @include sm {
               margin-right: 0; } }

         &.is-active {
            color: $color1;
            #{$parent}_underlined & {
               &::after {
                  opacity: 1; }
               span {
                  &:nth-child(1) {
                     opacity: 0; }
                  &:nth-child(2) {
                     opacity: 1; } } } }

         #{$parent}_sm & {
            font-size: 15px; }

         #{$parent}_underlined & {
            position: relative;
            padding: 0 10px;
            font-size: 15px;
            @include regFont;
            color: $color1;
            text-align: center;
            @include sm {
               padding-bottom: 5px; }

            &:not(:last-child) {
               @include sm {
                  margin-bottom: 10px; } }

            &::after {
               top: calc(100% + 10px);
               left: 0;
               width: 100%;
               height: 3px;
               opacity: 0;
               transition: .3s ease-in-out;
               @include sm {
                  top: calc(100% - 3px); } }
            &:last-child::after {
               display: block; }
            &:hover {
               span {
                  &:nth-child(1) {
                     opacity: 0; }
                  &:nth-child(2) {
                     opacity: 1; } } }
            span {
               transition: .3s ease-in-out;
               &:nth-child(2) {
                  position: absolute;
                  top: 0;
                  left: 0;
                  right: 0;
                  width: 100%;
                  opacity: 0;
                  @include f('pt700'); } } } } } }

// end tabs //
