// *** begin article *** //
.article {
   padding-bottom: 45px;
   @include md {
      padding-bottom: 20px; }
   &__container {
      @include md {
         padding: 0; } }
   &__title {
      margin: -10px 0 30px; }
   &__text {
      margin-bottom: 50px;
      p {
         &:not(:last-child) {
            margin-bottom: 20px; } } }
   &__sub-title {
      margin-bottom: 20px; }
   &__row {
      display: flex;
      margin: 0 -15px 80px;
      @include md {
         margin: 0 -15px 50px; }
      @include sm {
         flex-direction: column;
         margin: 0 0 30px; } }
   &__list {
      flex: 0 1 33.33333%;
      width: 33.33333%;
      padding: 0 15px;
      @include sm {
         padding: 0;
         width: 100%;
         flex: 1 1 auto; }
      &:not(:last-child) {
         @include sm {
            margin-bottom: 20px; } } }

   &__table {
      border: 1px solid #e2e1df;
      @include sm {
         min-width: 738px; }
      thead {
         border: 1px solid $color4;
         color: $color4;
         @include boldFont;
         tr {
            border-bottom: 1px solid $color4; }
         td {
            &:not(:last-child) {
               border-right: 1px solid $color4; } } }

      tr {
         border-bottom: 1px solid #e2e1df; }

      td {
         padding: 13px 20px;
         &:not(:last-child) {
            border-right: 1px solid #e2e1df; } }

      &-inner {
         @include sm {
            overflow-x: auto; } }

      &-title {
         margin-bottom: 38px; } } }

// end article //

// *** begin info *** //
.info {
   padding: 30px 0;
   @include sm {
      padding: 15px 0; }
   &__block {
      display: flex;
      align-items: center;
      min-height: 145px;
      margin-bottom: 20px;
      color: $white;
      background-color: #6600cc;
      @include sm {
         flex-direction: column; }
      &-icon-wrap {
         @include flexCenter;
         align-self: stretch;
         flex: 0 0 145px;
         width: 145px;
         background-color: #5c00b8;
         @include sm {
            flex: 1 1 auto;
            width: 100%;
            padding: 30px; } }
      &-icon {
         margin-left: 20px;
         margin-top: -10px; }
      &-text {
         flex: 1 1 auto;
         padding: 20px 60px;
         @include md {
            padding: 20px 40px; }
         @include sm {
            padding: 20px; } }

      &-title {
         margin: -15px 0 15px;
         @include boldFont;
         font-size: 38px;
         @include md {
            font-size: 32px; }
         @include sm {
            font-size: 27px; } }
      p {
         font-size: 21px;
         @include sm {
            font-size: 18px; } } }

   &__row {
      display: flex;
      @include sm {
         flex-direction: column; } }

   &__item {
      $parent: &;
      display: flex;
      flex: 0 1 50%;
      width: 50%;
      align-items: center;
      min-height: 145px;
      @include sm {
         width: 100%;
         flex: 1 1 auto;
         flex-direction: column;
         padding: 25px 20px 40px;
         max-width: 345px;
         margin: 0 auto; }

      &_yellow {
         background-color: #ffdf45; }
      &_blue {
         background-color: #7ae4e6;
         color: $white; }
      &-img-wrap {
         align-self: flex-end;
         flex: 0 0 160px;
         width: 160px;
         text-align: left;
         @include sm {
            flex: 0 1 auto;
            align-self: flex-start; } }
      &-content {
         position: relative;
         flex: 0 0 190px;
         margin-left: 20px;
         padding-left: 40px;
         @include md {
            margin-left: 0; }
         @include sm {
            flex: 0 1 auto;
            width: 250px; } }
      &-icon {
         position: absolute;
         top: 0;
         left: 0;
         font-size: 28px;
         #{$parent}_blue & {
            fill: $white; } }

      &-title {
         margin-bottom: 10px; }
      p {
         max-height: 54px; } } }
// end info //

// *** begin contact-block *** //
.contact-block {
   padding: 60px 0 0;
   @include md {
      padding: 20px 0 0; }
   &__heading {
      margin-bottom: 40px;
      @include md {
         margin-bottom: 20px; } }
   &__title {
      margin-bottom: 15px; }
   &__sub-title {
      font-size: 21px; }
   &__form {
      position: relative;
      padding: 20px 30px 30px;
      border: 1px solid #e2e1df;
      @include sm {
         padding: 15px 15px 20px; }
      &-wrap {}
      &-title {
         margin-bottom: 20px;
         padding-right: 30px; }
      &-close {
         position: absolute;
         top: 15px;
         right: 15px;
         &:hover {
            svg {
               fill: $color7; } }
         svg {
            font-size: 12px;
            transition: .3s ease-in-out; } }
      &-row {
         display: flex;
         margin: 0 -6px 12px;
         @include sm {
            flex-direction: column; } }
      &-field {
         flex: 0 1 33.33333%;
         width: 33.33333%;
         padding: 0 6px;
         @include sm {
            flex: 0 1 auto;
            width: 100%; }
         &:not(:last-child) {
            @include sm {
               margin-bottom: 10px; } }
         &_message {
            flex: 1 1 auto;
            width: 100%;
            padding: 0;
            margin-bottom: 12px; } }
      &-actions {
         &-btn {
            @include sm {
               width: 100%; }

            &:not(:last-child) {
               margin-right: 11px;
               @include sm {
                  margin: 0 0 10px; } } } } } }
// end contact-block //
