.page {
   padding: 53px 0 30px;
   @include md {
      padding: 30px 0; }
   &_text {
      padding: 75px 0 30px; }
   &__container {
      display: flex;
      align-items: flex-start;
      @include md {
         flex-direction: column; } }

   &__head-container {
      margin-bottom: 50px;
      @include md {
         margin-bottom: 30px; } }

   &__title {
      padding-right: 30px;
      &_auctions {
         @include md {
            display: none; } } }
   &__breadcrumbs {
      margin-bottom: 32px; }
   &__side {
      flex: 0 0 270px;
      width: 270px;
      @include md {
         flex: 1 1 auto;
         width: 100%;
         margin-top: 30px; }
      &_category {
         @include md {
            margin: 0 0 15px; } }
      &-block {
         padding: 28px 26px;
         border: 1px solid #e2e1df;
         @include md {
            padding: 20px; }
         &:not(:last-child) {
            margin-bottom: 40px;
            @include md {
               margin-bottom: 20px; } } }
      &-title {
         margin: -8px 0 20px; }
      &-descr {
         &:not(:last-child) {
            margin-bottom: 25px; } } }

   &__content {
      flex: 1 1 auto;
      padding-left: 40px;
      @include md {
         order: -1;
         width: 100%;
         padding: 0; }

      .is-ie & {
         max-width: 946px; } } }
