.slider {
   display: none;
   &.slick-initialized {
      display: block; } }

.slider-dots {
   $parent: &;
   display: flex;
   justify-content: center;
   line-height: 0;
   &_gray {
      button {
         background-color: #808080; } }
   &_black {
      button {
         background-color: $color2; } }
   li {
      &:not(:last-child) {
         margin-right: 24px; }
      &.slick-active {
         button {
            pointer-events: none;
            background-color: $color1; } }
      button {
         width: 6px;
         height: 6px;
         border-radius: 50%;
         @include hide-text;
         transition: .3s ease-in-out;
         &:hover {
            opacity: .5; } } } }

.slider-btn {
   position: absolute;
   top: calc(50% - 2px);
   transition: .3s ease-in-out;
   &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 194px;
      height: 194px;
      background-color: transparent;
      transition: .3s ease-in-out;
      content: '';
      @include md {
         height: 70px;
         width: 70px; } }
   &:hover {
      &::after {
         background-color: #ffe469; } }
   &_prev {
      left: 0;
      &::after {
         transform: translate(-48.5%, -50%); } }
   &_next {
      right: 0;
      &::after {
         transform: translate(-51.5%, -50%); } }
   .icon {
      position: relative;
      z-index: 1;
      @include sm {
         width: 32px; }
      &.icon-sld-arr-r {
         @include sm {
            background-position: -84px -22px; } } } }

// *** begin view *** //
.view {
   @include sm {
      display: flex;
      flex-direction: column; }
   &__nav {
      float: left;
      width: 103px;
      @include sm {
         order: 1;
         float: none;
         width: 100%; }
      &-item {
         margin-top: -2px;
         cursor: pointer;
         height: 104px !important;
         @include sm {
            position: relative;
            height: auto!important;
            padding-top: 6.3%; }
         img {
            @include cover-img;
            @include sm {
               @include coverdiv; } } } }

   &__block {
      float: left;
      width: calc(100% - 103px);
      padding-left: 7px;
      @include sm {
         float: none;
         width: 100%;
         padding-left: 0; }
      &-item {
         width: 510px;
         height: 510px;
         @include sm {
            height: auto; }
         img {
            @include cover-img; } } } }

// end view //
