.footer {
   padding: 50px 0;
   color: $color7;
   @include sm {
      padding: 25px 0 35px; }
   &__container {
      display: flex; }
   &__block {
      flex: 1 1 auto;
      padding-right: 58px;
      @include md {
         padding-right: 0; }
      &-line {
         display: flex;
         margin-bottom: 50px;
         @include r(1023) {
            flex-direction: column;
            margin-bottom: 20px; } }
      &-row {
         display: flex;
         justify-content: space-between;
         margin: 0 -25px;
         @include md {
            margin: 0 -15px; }
         @include sm {
            flex-direction: column; }
         &_social {
            margin: -7px 0 0;
            padding-right: 28px;
            @include md {
               margin: 0; }
            @include r(1023) {
               padding: 0 30px; }
            @include sm {
               flex-direction: row;
               margin-bottom: 20px; } } }

      &-col {
         padding: 0 25px;
         flex: 0 1 percentage(250 / 955);
         width: percentage(250 / 955);
         @include md {
            padding: 0 15px; }
         &:nth-child(1) {
            flex: 0 0 percentage(250 / 955);
            width: percentage(250 / 955);
            @include r(1023) {
               flex: 0 0 30%;
               width: 30%; }
            @include sm {
               order: 1;
               flex: 0 1 auto;
               width: 100%;
               margin-top: 20px;
               text-align: center; }
            .is-ie & {
               flex: 0 1 33%; } }

         &:nth-child(2),
         &:nth-child(3) {
            @include r(1023) {
               display: none; } }

         &:nth-child(4) {
            display: flex;
            flex-direction: column;
            flex: 0 0 percentage(324 / 955);
            width: percentage(324 / 955);
            @include r(1023) {
               flex: 0 1 70%;
               width: 70%; }
            @include sm {
               flex: 0 1 auto;
               width: 100%; } } } }

   &__logo {
      margin-right: 65px;
      @include r(1023) {
         margin-right: 0;
         flex: 0 1 auto; }
      @include sm {
         margin: 0 auto; } }

   &__rights {
      padding-top: 10px;
      font-size: 14px;
      &-text {
         margin-bottom: 88px;
         @include r(1023) {
            margin-bottom: 55px; }
         @include sm {
            margin-bottom: 15px; } }
      &-links {
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         @include sm {
            align-items: center; }
         a {
            color: $color1;
            &:not(:last-child) {
               margin-bottom: 16px; } } } }

   &__menu {
      display: flex;
      align-items: center;
      margin-top: 13px;
      @include r(1023) {
         justify-content: center;
         margin-top: 20px; }
      @include sm {
         flex-direction: column;
         align-items: flex-start;
         margin: 20px auto 0; }

      &-item {
         &:not(:last-child) {
            margin-right: 36px;
            @include r(1023) {
               margin-right: 30px; }
            @include sm {
               margin: 0 0 15px; } }
         &_has-select {
            display: flex;
            align-items: center;
            .icon-location {
               fill: $color7; }
            p {
               display: flex;
               align-items: center;
               margin-right: 5px;
               svg {
                  font-size: 28px;
                  margin: 0 7px;
                  z-index: 1; } } } }
      &-link {
         display: flex;
         align-items: flex-end;
         color: $color1;
         &:hover {
            svg {
               fill: $color1; } }
         span {
            margin-left: 12px; }
         svg {
            fill: $color7;
            font-size: 20px;
            transition: .3s ease-in-out;
            @include sm {
               width: 30px; } }
         .icon-hammer {
            font-size: 20px; }
         .icon-beg {} }
      &-select {} }

   &__title {
      margin-bottom: 44px;
      @include r(1023) {
         margin-bottom: 25px; }
      @include sm {
         text-align: center; } }
   &__list {}
   &__subscribe {
      margin-top: auto; }

   &__social-block {
      padding: 10px 0 0;
      flex: 0 0 263px;
      @include md {
         display: none; } } }
