// *** begin button *** //
.btn {
   display: inline-flex;
   justify-content: center;
   padding: 13px 10px;
   outline: 0;
   @include f('pt400', 15);
   line-height: 1.2;
   text-align: center;
   &_black {
      color: $color1;
      border: 1px solid currentColor;
      transition: .3s ease-in-out;
      &:hover {
         color: $color4; } }
   &_purple {
      color: $color4;
      border: 1px solid currentColor; }
   &_sm {
      min-width: 135px; }
   &_md {
      min-width: 174px; }
   &_gray {
      color: $color7;
      border: 1px solid #e2e1df; }

   &.is-disabled {
      opacity: .3;
      pointer-events: none; }
   &_has-icon {
      position: relative;
      padding: 13px 5px 13px 45px;
      svg {
         fill: $color7; }
      .icon-hammer {
         font-size: 22px; } }
   &__icon {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 45px;
      flex: 0 0 45px;
      @include flexCenter;
      border-right: 1px solid #e2e1df; } }

// end button //

// *** begin link *** //
.link {
   $parent: &;
   display: inline-flex;
   align-items: center;
   color: $color1;
   &_bold {
      @include f('pt700'); }
   &_white {
      color: $white; }
   &_blue {
      color: $color3; }
   &_black {
      color: $color1;
      transition: .3s ease-in-out;
      &:hover {
         color: $color7; } }
   &_decor {
      display: inline;
      span {
         border-bottom: 1px solid currentColor;
         transition: .3s ease-in-out; }
      &:hover {
         span {
            border-bottom-color: transparent; } } }
   &__icon {
      margin-left: 7px;
      #{$parent}:hover & {
         animation: smMoveRight .3s ease-in-out; }
      #{$parent}_bold & {
         margin-left: 11px; }
      #{$parent}_white & {
         fill: $white; }
      &.icon-zoom {
         font-size: 12px;
         animation: none!important;
         transition: .3s ease-in-out;
         #{$parent}:hover & {
            fill: $color7; } } } }
// end link //

// *** begin help-btn *** //
.help-btn {
   $parent: &;
   @include flexCenter;
   position: relative;
   width: 44px;
   height: 44px;
   background-color: #6600cc;
   transition: .3s ease-in-out;
   &:hover {
      background-color: darken(#6600cc, 10); }
   &_yellow {
      width: 100px;
      height: 100px;
      background-color: #ffe987;
      &:hover {
         background-color: #ffe469; } }
   svg {
      fill: $white;
      font-size: 30px; }
   &__text {
      position: absolute;
      left: calc(100% + 55px);
      width: 80px;
      @include f('pt700', 21);
      color: $color9;
      transition: .3s ease-in-out;
      @include sm {
         left: calc(100% + 20px); }
      &:hover {
         color: $color1; }
      #{$parent}_yellow & {
         left: calc(100% + 55px);
         color: $color7;
         @include sm {
            left: calc(100% + 20px); }
         &:hover {
            color: $color1; } } } }
// end help-btn //

// *** begin add-btn *** //
.add-btn {
   $parent: &;
   display: flex;
   font-size: 17px;
   border: 1px solid #393f4d;
   &__icon {
      @include flexCenter;
      flex: 0 0 70px;
      width: 70px;
      height: 70px;
      border-right: 1px solid #393f4d;
      transition: .3s ease-in-out;
      @include sm {
         width: 50px;
         height: 50px; }
      #{$parent}:hover & {
         background-color: #ffe469; } }
   &__text {
      @include flexCenter;
      flex: 1 1 auto;
      padding: 15px 30px;
      text-align: center;
      transition: .3s ease-in-out;
      #{$parent}:hover & {
         background-color: #ffe469; } } }
// end add-btn //
