// *** begin category *** //
.category {
   .page__content {
      @include md {
         display: flex;
         flex-direction: column; } }
   &__breadcrumbs {
      margin: -5px 0 45px;
      @include md {
         margin: -5px 0 20px; } }
   &__title {
      margin-bottom: 43px;
      @include md {
         margin-bottom: 30px; } }
   &__filter {
      margin-bottom: 22px; }
   &__checkbox-group {
      &:not(:last-child) {
         margin-bottom: 25px; } }
   &__row {}
   &__inner {
      margin-bottom: 50px; }
   &__descr {
      padding: 80px 0 0;
      @include md {
         padding: 0 0 50px; }
      &-title {
         margin-bottom: 30px; }

      p {
         &:not(:last-child) {
            margin-bottom: 20px; } } } }

// end category //

// *** begin teaser *** //
.teaser {
   position: relative;
   @include md {
      order: 1; }
   &__bg {
      pointer-events: none;
      @include coverdiv;
      @include cover;
      @include r(1023) {
         background-position: 75% 50%; }
      @include sm {
         background-size: contain;
         background-position: 100% 100%; } }
   &__inner {
      position: relative;
      display: flex;
      align-items: center;
      padding: 45px 40px 40px 65px;
      background-color: #6600cc;
      @include r(1023) {
         padding: 45px 30px 40px 30px; }
      @include sm {
         padding: 30px 20px 40px; } }
   &__text {
      flex: 0 0 390px;
      color: #fff;
      z-index: 2;
      @include r(1023) {
         flex: 0 0 300px; }
      @include sm {
         flex: 0 0 200px; } }
   &__logo {
      margin-bottom: 30px; }
   &__title {
      margin-bottom: 27px; }
   &__descr {
      max-width: 340px;
      margin-bottom: 30px;
      font-size: 25px; }
   &__link {
      font-size: 17px; }
   &__help-btn {
      margin-left: -100px;
      @include md {
         margin: 15px 0 0; } } }
// end teaser //
