.screen {
   $parent: &;
   position: relative;
   &_full {
      background-color: #edeff1;
      height: 492px;
      overflow: hidden;
      @include sm {
         height: auto; } }
   &_full-2 {
      height: 494px;
      background-color: #aff1f2;
      overflow: hidden;
      @include sm {
         height: auto; } }
   &_small {
      padding: 35px 0 30px;
      background-color: #ffe987; }
   &__inner {
      position: relative;
      max-width: 1920px;
      margin: 0 auto;
      height: 100%;
      #{$parent}_full & {
         @include sm {
            padding: 40px 0; } } }
   &__top {
      padding: 40px 0;
      background: #fff; }
   &__container {
      position: static;
      display: flex;
      align-items: center;
      height: 100%;
      #{$parent}_small & {
         justify-content: space-between;
         padding-right: 60px;
         @include md {
            flex-direction: column;
            padding-right: 0;
            align-items: flex-start; }
         @include sm {
            padding-right: 15px; } }
      #{$parent}_full-2 & {
         @include md {
            position: relative; }
         @include sm {
            padding: 40px 15px; } } }
   &__content {
      flex: 0 0 490px;
      @include md {
         flex: 0 0 358px; }
      @include sm {
         flex: 0 1 290px; }
      #{$parent}_full &,
      #{$parent}_full-2 & {
         z-index: 1; } }
   &__logo {
      display: block;
      width: 198px;
      margin-bottom: 45px;
      @include md {
         margin-bottom: 25px; }
      #{$parent}_small & {
         margin-bottom: 10px;
         @include md {
            flex: 0 1 auto; } } }
   &__title {
      margin-bottom: 32px;
      #{$parent}_full &,
      #{$parent}_full-2 & {
         @include md {
            font-size: 38px; } }
      #{$parent}_full & {
         @include sm {
            max-width: 200px;
            color: $color1; } }

      #{$parent}_small & {
         flex: 0 0 350px;
         margin: -10px 0 0;
         font-size: 38px;
         color: $color1;
         @include md {
            flex: 0 1 auto;
            margin: 0 0 15px; } } }
   &__sub-title {
      max-width: 350px;
      margin-bottom: 50px;
      font-size: 29px;
      @include md {
         margin-bottom: 30px;
         font-size: 22px; }
      #{$parent}_full-2 & {
         @include md {
            color: $white; } }
      #{$parent}_small & {
         margin-bottom: 30px;
         max-width: none;
         @include f('pt700');
         font-size: 21px; } }

   &__link {
      margin-left: -3px;
      font-size: 17px;
      #{$parent}_full-2 & {
         @include md {
            color: $white;
            .link__icon {
               background-position: -138px -68px; } } } }

   &__img-wrap {
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      #{$parent}_full & {
         flex: 0 0 907px;
         width: 907px;
         @include r(1599) {
            position: static; }
         @include sm {
            position: absolute;
            left: 60%;
            right: auto; } }

      #{$parent}_full-2 & {
         flex: 0 0 758px;
         width: 758px;
         @include md {
            flex: 0 1 auto;
            width: 100%; } }

      img {
         max-height: 100%;
         @include cover-img;
         @include sm {
            object-position: 20%; } } }

   &__text-content {
      flex: 0 0 410px;
      @include md {
         flex: 0 1 auto; } } }
