// *** begin logo *** //
.logo {
   flex: 0 0 198px;
   width: 198px;
   height: 36px;
   &_long {
      svg {
         pointer-events: none;
         overflow: visible; } } }

// end logo //

// *** begin moving-line *** //
.moving-line {
   opacity: 0;
   pointer-events: none;
   position: absolute;
   bottom: 0;
   left: 0;
   width: 100%;
   height: 3px;
   background-color: $color1;
   transition: .3s ease-in-out; }
// end moving-line //

// *** begin socials *** //
.socials {
   display: flex;
   align-items: center;
   a {
      display: flex;
      align-items: center;
      &:not(:last-child) {
         margin-right: 23px; }
      &:hover {
         svg {
            fill: $color1; } }
      svg {
         fill: #acafbd;
         transition: .3s ease-in-out; }
      .icon-fb {
         font-size: 17px; }
      .icon-vk {
         font-size: 20px; }
      .icon-inst {} } }
// end socials //

// *** begin subscribe *** //
.subscribe {
   &__form {
      display: flex;
      margin: 0 -1px;
      &-field {
         flex: 1 1 50%;
         width: 50%;
         padding: 0 1px; }

      &-submit-btn {
         width: 100%;
         padding: 13px 15px; } } }
// end subscribe //

// *** begin dropdown *** //
.dropdown {
   $parent: &;
   position: relative;
   text-align: center;
   cursor: pointer;
   > ul {
      position: relative;
      cursor: pointer;
      &:hover ul {
         pointer-events: auto;
         opacity: 1;
         transform: scaleY(1); }
      &:hover {
         #{$parent}__icon {
            fill: $color1; }
         #{$parent}__icon-arr {
            animation: smallMoveDown .5s ease; } }
      > li > a {
         position: relative;
         z-index: 1;
         pointer-events: none;
         span {
            color: $color1; } }
      li {
         &:not(:last-child) {
            margin-bottom: 5px; }
         a {
            display: flex;
            align-items: center;
            cursor: pointer;
            color: $color7;
            white-space: nowrap;
            transition: .3s ease-in-out;
            &:hover {
               color: $color1; } } }
      ul {
         pointer-events: none;
         position: absolute;
         top: -15px;
         left: -20px;
         right: 0;
         transform-origin: 0 0;
         opacity: 0;
         transform: scaleY(0);
         display: table;
         padding: 42px 15px 10px 44px;
         min-width: 130px;
         background-color: #f6f6f6;
         transition: .3s ease-in-out; } }

   &__icon {
      margin-right: 5px;
      font-size: 28px;
      transition: .3s ease-in-out; }
   &__icon-arr {
      margin: 3px 0 0 6px;
      font-size: 9px; } }
// end dropdown //

.breadcrumbs {
   &_default {
      margin-bottom: 30px; }
   &_full {
      padding: 50px 0;
      @include md {
         padding: 25px 0; } }
   ul {
      font-size: 0;
      li {
         display: inline-block;
         vertical-align: middle;
         line-height: 22px;
         &:not(:last-child) {
            margin-right: 3px; }
         &:not(:last-child)::after {
            display: inline-block;
            vertical-align: middle;
            margin-left: 5px;
            pointer-events: none;
            font-size: 15px;
            content: '/'; }
         &.is-active a {
            pointer-events: none;
            color: $color1; }
         a {
            display: inline-block;
            vertical-align: middle;
            font-size: 15px;
            color: $color7;
            @include trs;
            &:hover {
               color: $color1; } } } } }

.pagination {
   text-align: center;
   &_default {
      margin: 50px 0 0;
      @include sm {
         margin: 30px 0 0; } }

   ul {
      margin-bottom: 13px;
      font-size: 0;
      line-height: 0;
      li {
         display: inline-block;
         vertical-align: middle;
         position: relative;
         margin: 0 12px;
         &.is-active {
            a {
               font-size: 21px;
               color: $color1;
               pointer-events: none; } }

         a {
            text-align: center;
            @include f('pt700', 10, 10);
            color: $color7;
            @include trs;
            &:hover {
               color: $color1;
               svg {
                  fill: $color7; } } } } }

   &__arr {
      font-size: 11px;
      fill: $color1;
      @include trs;
      &_l {
         transform: rotate(90deg); }
      &_r {
         transform: rotate(-90deg); } } }

// *** begin menu *** //
.menu {
   &__block {
      padding-bottom: 30px;
      border-bottom: 1px solid #e2e1df;
      &-item {
         display: flex;
         align-items: center;
         height: 22px;
         &:hover {
            svg {
               fill: $color1; } }
         &:not(:last-child) {
            margin-bottom: 11px; }
         svg {
            margin-right: 6px;
            width: 30px;
            fill: $color7;
            font-size: 20px;
            transition: .3s ease-in-out; }

         .icon-user {
            font-size: 22px; }
         .icon-ring {
            font-size: 22px; }
         .icon-cloud-2 {
            font-size: 17px; } } }

   &__list {
      padding-top: 30px;
      li {
         &:not(:last-child) {
            margin-bottom: 7px!important; } } } }
// end menu //

// *** begin list *** //
.list {
   $parent: &;
   color: $color7;
   li {
      &:not(:last-child) {
         margin-bottom: 1px; } }
   a {
      transition: .3s ease-in-out;

      &:hover {
         color: $color1; }
      svg {
         position: absolute;
         top: 4px;
         left: 0;
         transform: rotate(-90deg);
         opacity: 0;
         font-size: 9px;
         transition: .3s ease-in-out; } }

   &_has-icon {
      li {
         &:not(:last-child) {
            margin-bottom: 7px; } }
      a {
         position: relative;
         display: inline-block;
         padding-left: 15px;
         margin-left: -15px;
         &:hover {
            transform: translateX(15px);
            svg {
               opacity: 1;
               transform: rotate(-90deg); } } } } }

// end list //

// *** begin user-info *** //
.user-info {
   display: flex;
   align-items: flex-start;
   font-size: 13px;
   span {
      &:not(:last-child) {
         margin-right: 5px; }
      &:nth-child(2) {
         white-space: nowrap; } }
   svg {
      margin: 0 3px;
      vertical-align: 0;
      fill: $color1;
      font-size: 11px; } }
// end user-info //
